<template>
  <div class="info-query-cont">
    <div class="query-head">
      <div class="query-warp">
        <input type="text" v-model="quertText" maxlength="11" @focus="handleQueryFocus()" @blur="handleQueryBlur()" placeholder="请搜索姓名、手机号、固定电话">
        <i class="el-icon-close icon-el" @click="handleClearInput()" :class="{ 'show' : clearIconFlag }"></i>
      </div>
    </div>
    <div class="query-pane">
      <cust-title titleName="用户资料"></cust-title>
        <div class="wrap-table">
          <el-table
            :data="tableData"
            border
            style="width: 100%">
            <el-table-column
              prop="name"
              label="姓名">
            </el-table-column>
            <el-table-column
              prop="cardType"
              label="证件类型">
            </el-table-column>
            <el-table-column
              prop="cardId"
              label="证件号码">
            </el-table-column>
            <el-table-column
              prop="tel"
              label="手机号">
            </el-table-column>
            <el-table-column
              prop="staticTel"
              label="固定号码">
            </el-table-column>
            <el-table-column label="操作">
              <template slot-scope="scope">
                <span class="operate" @click="handleShowUserQueryPane(scope.raw)">详情</span>
              </template>
            </el-table-column>
          </el-table>
        </div>
    </div>

  </div>
</template>

<script>
import CustTitle from '@/components/CustTitle/CustTitle'

export default {
  components: { CustTitle },
  data () {
    return {
      quertText: '',
      clearIconFlag: false,

      tableData: [{
        name: '王小虎',
        cardType: '身份证',
        cardId: '4324243535435',
        tel: '124234324',
        staticTel: ''
      }]
    }
  },
  mounted () {
    let self = this
    document.onkeydown = function (event) {
      console.log('quertText', self.quertText)
    }
  },
  methods: {
    handleClearInput () {
      this.quertText = ''
    },
    handleQueryFocus () {
      this.clearIconFlag = true
    },
    handleQueryBlur () {
      this.clearIconFlag = false
    },
    handleShowUserQueryPane (rowData) {
      this.$store.dispatch('SetQueryPane', true).then(() => {
        console.log('---- 打开查询面板 -----')
      })
    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>

  .info-query-cont{
    position: relative;
    height: 100%;
    padding-bottom: 40px;
    overflow-x: hidden;
    overflow-y: scroll;
    .query-head{
      width: 100%;
      height: 100px;
      .query-warp{
        @include center;
        width: 500px;
        height: 100px;
        border-bottom: 1px solid #E9E9E9;
        input {
          width: 100%;
          height: 100%;
          font-size: 24px;
          color: #000000;
          text-align: center;
        }

        .icon-el{
          @include vetical;
          right: 10px;
          font-size: 35px;
          color: rgba(0,0,0,0.65);
          opacity: 0;
          &.show{
            opacity: 1;
          }
        }
      }
      :-moz-placeholder {
        font-size: 24px;
        color: #ccc;
      }

      ::-moz-placeholder {
        font-size: 24px;
        color: #ccc;
      }

      input:-ms-input-placeholder{
        font-size: 24px;
        color: #ccc;
      }

      input::-webkit-input-placeholder{
        font-size: 24px;
        color: #ccc;
      }
    }

    .query-pane{
      position: relative;
      width: 100%;
      padding-left: 40px;
      padding-right: 40px;
    }
    .wrap-table {
      position: absolute;
      margin-top:10px;
      width:1000px;
      .operate{
        display: block;
        width: 100%;
        text-align: center;
        font-size: 12px;
        color: #4A90E2;
        line-height: 18px;
      }
    }
  }
</style>



